<template>
	<div style="width: 100%;">
		<div class="cart-empty" v-if='!food.count' @click.stop.prevent='oneplus'>加入购物车</div>
		<div class="cart-full" v-show='food.count'>
			<span class="cart-min" @click.stop.prevent='minus'><i>-</i></span>
			<input type="text" class="cart-num" :value="food.count" ref='cartnum' @blur='inputdone'>
			<span class="cart-plus" @click.stop.prevent='plus'><i>+</i></span>
		</div>
	</div>
</template>

<script type="text/javascript">
import {mapGetters,mapMutations} from 'vuex'
import Vue from 'vue'
	export default{
		props:{
			foodid:{
				type:Number
			},
			food:{
				type:Object
			}
		},
		mounted(){
			// setTimeout(() => {
			// 	this.$refs.cartnum.value = this.food.count
			//   // Todo...
			// }, 50)
		},
		methods:{
			...mapMutations({
				setlist : 'SET_LIST',
				setfood : 'SET_FOOD'
			}),
			minus(){
				this.countnum  = parseInt(this.food.count) -1
				if(this.countnum  <0){
					this.countnum = 0
				}
				// this.$refs.cartnum.value = this.countnum
			},
			inputdone(){
				if(parseInt(this.$refs.cartnum.value) < 0 ){
					this.$refs.cartnum.value = 0
					this.countnum = 0
					this.food.count  =0 
				} else{
					this.food.count = parseInt(this.$refs.cartnum.value)
					this.countnum = parseInt(this.$refs.cartnum.value)
				}
			},
			plus(){
				this.countnum =this.food.count + 1
			},
			oneplus(){
				this.countnum = 1
				Vue.set(this.food,'count',1)
			},
			countchange(now){
				
				// this.setfood(this.food)
			}
		},
		computed:{
			...mapGetters([
					'list'
				]),

		},
		data(){
			return {
				countnum:0,
				accountlist:[]
			}
		},
		watch:{
			countnum(now,before){
				Vue.set(this.food,'count',now)
				this.$refs.cartnum.value = this.countnum
				// console.log(this.food.count)
				
			}
		}
	}
</script>

<style type="text/css" lang='less'>

	.cart-empty{
		width: 90px;
		height: 26px;
		text-align: center;
		border-radius: 13px;
		font-size: 12px;
		line-height: 26px;
		background-color: #0089dc;
		color: white;
		cursor: pointer;
		z-index: 33;
	}
	.cart-full{
		width: 100%;
		display: flex;
		.cart-num{
			display: inline-block;
			width: 36px;
			height: 24px;
			text-align: center;
			vertical-align: top;
			border: none;
			text-decoration: none;
			font-size: 12px;
		}
		.cart-min,
		.cart-plus{
			display: inline-block;
			width: 	27px;
			height: 22px;
			line-height: 24px;
			background-color: #ddd;
			text-align: center;
			font-size: 14px;
			cursor: pointer;
		}
		.cart-min{
				border-top-left-radius: 50%;
				border-bottom-left-radius: 50%;
		}
		.cart-plus{
				border-top-right-radius: 50%;
				border-bottom-right-radius: 50%;
		}
	}
</style>


