import * as types from './types.js'

const mutations = {
	[types.SET_LIST](state,list){
		state.list = list
	},
	[types.SET_FOOD](state,food){
		for(let i = 0;i<state.list.length;i++){
			state.list[i].foods.forEach((item) => {
			  	if(item.virtual_food_id === food.virtual_food_id){
			  		item = food
			  	}
			})
		}
	},
	[types.SET_CURRENTCARD](state,food){
		state.currentcard = food
	},
	[types.SET_FOOD_LIST](state,foodlist){
		state.foodlist = foodlist
	}
}

export default mutations


