<template>
	<div class="checklist">
		<div class="bill-header">
			确认订单
		</div>
		<div class="bill-wrap">
			<div class="bill-left">
				<table class="bill-table">  
					<caption style="height: 60px;line-height: 60px;font-size: 18px;">订单详情</caption>
					<tr style="width: 200px;">
						<td>商品</td>
						<td>份数</td>
						<td>小计（元）</td>
					</tr>
					<tr v-for='item in paylist'>
						<td>{{item.name}}</td>
						<td>{{item.count}}</td>
						<td>{{item.specfoods[0].price}}</td>
					</tr>
					<tr>
						<td>配送费</td>
						<td></td>
						<td>4元</td>
					</tr>
					<tr><th colspan ="3" height="60" style="font-size: 46px;color:red; text-align: right;padding-right: 30px;">{{getdata}}元</th></tr>
				</table>
			</div>
			<div class="bill-right">
				<div class="bill-address">
					<h4>收货地址</h4>
					<div>添加新地址</div>
				</div>
				<div class="bill-check">
					<h4>付款方式</h4>
					<span>推荐使用在线支付，不用找零，优惠更多</span>
					<div>
						<span>支付宝</span>
						<span>微信</span>
					</div>
				</div>
				<div class="bill-privilege">
					<h4>选择优惠</h4>
					<div>
						<select>
						  <option>-10元</option>
						  <option>-10元</option>
						  <option>-10元</option>
						  <option>-10元</option>
						</select>
					</div>
					
				</div>
				<div class="bill-other">
					
				</div>
				<button class="bill-submit" @click='guess'>提交订单</button>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
import {mapGetters,mapMutations} from 'vuex'

	export default{
		computed:{
			...mapGetters([
					'paylist'
				]),
			getdata(){
				let res = 0
				this.paylist.forEach((item) => {
					res = res+ item.count*item.specfoods[0].price
				  // Todo...
				})
				return res+4
			}
		},
		mounted(){
			setTimeout(() => {
			 	console.log(this.paylist)
			}, 20)
		},
		data(){
			return{
				
			}
		},
		methods:{
			guess(){
				window.location.href ='http://www.wuhanmarathon.org'
			}
		}
	}
</script>

<style type="text/css" lang='less'>
	.checklist{
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
		background-color: rgba(3,133,133,1);
		.bill-header{
			margin: 0 auto;
			height: 60px;
			width:1080px;
			line-height: 60px;
		}
		.bill-wrap{
			display: flex;
			width: 1080px;
			margin:0 auto;
			justify-content: space-between;
		}
		.bill-left{
			padding: 40px;
			background-color: #e0e0e0;
			float: left;
		}
		.bill-right{
			margin-left: 20px;
			padding: 25px 20px 150px 30px;
		    width:600px;
		    border: 1px solid #e0e0e0;
		    .bill-address{
				h4{
					font-size: 16px;
					font-weight: bold;
					text-align: left;
				}
				div:nth-child(2){

					width: 80%;
				    line-height: 72px;
				    background: #f1f1f1;
				    text-align: center;
				    color: #666;
				    margin:30px auto;
				}
		    }
		    .bill-check{
		    	text-align: left;
		    	h4{
		    		display: inline-block;
		    		font-size: 16px;
					font-weight: bold;
					text-align: left;
					margin-bottom: 30px;
		    	}
		    	span{
		    		margin-left: 20px;
		    		font-size: 12px;
		    		color: black;	
		    	}
		    	div{
		    		display: flex;
		    		justify-content: space-around;
		    		span{
		    			width: 45%;
		    			height: 70px;
		    			background: #f1f1f1;
					    text-align: center;
					    color: #666;
					    line-height: 70px;
		    		}
		    	}
		    }
		    .bill-privilege{
		    	margin:30px;
		    	h4{
		    		line-height: 40px;
		    	}
		    	display: flex;
		    	div{
		    		margin-left: 30px;
		    		select{
		    			width: 100px;
		    			height: 30px;
		    		}
		    	}
		    }
		}
	}
	table{
		border-collapse: collapse;
		width: 300px;
		border: 2px solid pink;
		td{
			height: 30px;
		}
	}
	.bill-submit{
		width: 100px;
		height: 40px;
	    display: inline-block;
	    padding: 8px 17px;
	    line-height: 1;
	    border: none;
	    border-radius: 2px;
	    outline: 0;
	    cursor: pointer;
	    font-size: 14px;
	    text-align: center;
	    background-color: #f74342;
	    color: white;
	    font-size: 16px;
	}
</style>


