<template>
  <div class="hello w">
      <div class="shoptype">
        <div>
          <router-link to="/restaurant">附近的餐厅</router-link>
        </div>
<!--         <div>
          <router-link to="/restaurant">附近的餐厅</router-link>
        </div> -->
      </div>
      <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
  .hello{
    .shoptype{
      display: flex;
      & div:first-child{
        margin-right: 30px;
        font-size: 18px;
      }
      & div:hover{
        border-bottom: 2px solid red;
      }
    }
  }

</style>



