<template>
	<div >
		<div class="slider-items">
				<div class="slider-img">
					<a href="#">
					<transition name='slide'>
						<img :src="imgarr[currentindex]" v-if='imgshow'>
					</transition>
					<transition name='slide-next' v-if='!imgshow'>
						<img :src="imgarr[currentindex]">
					</transition>
					</a>
				</div>
			<div class="slider-num">
				<span v-for='(item,index) in imgarr' :class="{spanstyle:currentindex===index}" @click='pagechange(index)'>{{index+1}}</span>
			</div>
			<div class="arrowspan">
				<span @click='prev'><</span><span @click='next'>></span>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:{
			imgarr:{
				type:Array,
				default:[]
			}
		},
		data(){
			return{
				currentindex:0,
				timer:'',
				imgshow:false
			}
		},
		mounted(){
			this.$nextTick(()=>{
				this.timeup()
			})
		},
		methods:{
			pagechange(index){
				clearInterval(this.timer)
				this.currentindex = index
				this.timeup()
			},
			timeup(){
					this.timer = setInterval(() => {
						this.next()
				}, 2000)
			},
			next(){
					this.currentindex += 1
				  if(this.currentindex === this.imgarr.length){
				  	this.currentindex = 0
				  }
			},
			prev(){
					this.currentindex = this.currentindex -1
				  if(this.currentindex < 0){
				  	this.currentindex = this.imgarr.length -1 
				  }
			}
		},
		watch:{
			currentindex(){
				this.imgshow = !this.imgshow
				console.log(this.imgshow,new Date())
				setTimeout(() => {
				  this.imgshow = false
				}, 500)
			}
		}
	}
</script>

<style type="text/css" lang='less'>
	.slider-items{
		width: 800px;
		height: 600px;
		position: relative;
		border: 1px solid red;
		.slider-img{
			white-space: nowrap;
			width: 100%;
			a{
				display: block;
				img{
					
					width: 100%;
					height: 600px;
					position: absolute;
					top:0;
				}
			}			
		}
		.slider-num{
			position: absolute;
			bottom: 10px;
			right: 20px;
			span{
				display: inline-block;
				width: 30px;
				height: 30px;
				line-height: 30px;
				border-radius: 50%;
				background-color: white;
				margin-left: 15px;
			}
		}
		.arrowspan{
			height: 100px;
			width: 100%;
			position: absolute;
			left: 0;
			bottom: 50%;
			color:white;
			span:nth-child(1){
				position: absolute;
				left: 0;
				display: inline-block;
				line-height: 100px;
				font-size: 80px;
			}
			span:nth-child(2){
				position: absolute;
				right: 0;
				display: inline-block;
				line-height: 100px;
				font-size: 80px;
			}
		}
	}
	.spanstyle{
		font-size: 20px;
		color: red;
	}
	.slide-enter-active{
		transition: all 0.5s;
	}
	.slide-enter{
		transform: translateX(800px);
	}
	.slide-next-leave-active{
		transform: translateX(-800px);
		transition: all 0.5s
	}
	.slide-next-leave-active{
		
	}
</style>


