export const list = state => state.list

export const paylist = state =>{
	let foodArr = []
	let arr = state.list
		for(let i = 0 ;i<arr.length;i++){
			arr[i].foods.forEach((item) => {
				if(item.count){
					foodArr.push(item)
				}				
			})
		}
	return foodArr
}

export const currentcard = state => state.currentcard

export const curfood = state => state.curfood

export const foodlist = state => state.foodlist




