<template>
	<div class="mini-card">
		<div class="mini-name">{{food.name}}</div>
		<div class="mini-supports" v-if="food.supports.length !== 0">
			<div v-for='item in food.supports' class="mini-support">
				<span>{{item.icon_name}}</span>
				<span>{{item.description}}</span>
			</div>
		</div>
		<div class="mini-delivery">
			<span>配送费{{food.float_delivery_fee}}元</span>
			<span>平均{{food.order_lead_time}}分钟送达</span>
		</div>
		<div class="mini-description">
			{{food.promotion_info}}
		</div>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:{
			food:{
				type:Object
			}
		},
		mounted(){
			console.log(this.food.distance)
		}
	}
</script>

<style type="text/css" lang='less'>
	.mini-card{
		top: 0;
		left: 310px;
		width: 290px;
		padding: 10px;
		background-color: skyblue;
		.mini-name{
			font-size: 16px;
			line-height: 34px;
			height: 34px;
			font-weight: 700;
			border-bottom: 1px solid gray;
		}
		.mini-supports{
			display: flex;
			flex-direction: column;
			.mini-support{
				margin: 10px 0;
				text-align: left;
				display: flex;
				span{
					display: inline-block;
					font-size: 12px;
					text-align: left;	
					margin-right: 10px;					
				}
			}
			.mini-support span:nth-child(1){
				border: 1px solid gray;
				width:16px;
				height: 16px;
				text-align: center;
			}

		}
		.mini-delivery{
			font-size: 14px;
			padding: 5px;
			margin: 10px;
			text-align: left;
			background-color: rgba(0,0,0,.1);
			span{
				padding:0 10px;
			}
			span:nth-child(1){
				padding:0 10px;
				border-right: 1px solid gray;
			}
		}
		.mini-description{
			font-size: 12px;
			text-align: left;
			line-height: 20px;
			padding: 10px;
		}	
	}
</style>


