<style type="text/css" lang='less'>
	.foodwrapper{
		display: flex;
		flex-wrap: wrap;
		margin-right: 1px;
		background-color: white;
		.foodcard{
			position: relative;
			width: 295px;
			height: 138px;
			display: flex;
			.foodleft{
				height: 98px;
				padding: 20px;
				position: relative;
				display: flex;
				flex-direction: column;
				.foodimg{
					width: 70px;
					height: 70px;
					img{
						display: block;
						position: relative;
						height: 70px;
						width: 70px;
					}
				}
				.deliverytime{
					font-size: 12px;
					line-height: 28px;
				}
			}
			.foodright{
				display: flex;
				flex-direction: column;
				padding: 20px 0;
				text-align: left;
				justify-content: space-around;
				.foodname{
					font-size: 16px;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					width: 165px;
				}
				.foodranting{
					span{
						font-size: 12px;
					}
				}
				.fooddeverycost{
					font-size: 12px;
				}
				.foodsupport{
					font-size: 12px;
					box-sizing: border-box;
					span{
						font-size: 12px;
						border: 1px solid #14ae18;
						color:red;
						border-radius: 3px;
						padding:3px 6px;
					}
				}
			}
		}
	}
	.fooddetail{
		position: absolute;
		left: 295px;
		display: none;
		z-index: 100;
	}
	.sequence{
		cursor:pointer;
	    text-align: left;
	    span{
	      display: inline-block;
	      width: 200px;
	      height: 40px;
	      font-size: 16px;
	      line-height: 40px;
	      font-weight: 300;
	      text-align: center;
	      &:hover{
	        background-color: pink;
	      }
	    }
	  }
</style>


