<template>
	<div class="detailwrapper w">
		<div class="shop-cart">
			<div class="cart-header">
				<div>
					<span>购物车</span>
					<span @click='emptycart'>[清空]</span>	
				</div>
				<span class="shop-cart-icon"><i class="fa fa-shopping-cart"></i></span>
			</div>
			<div class="cart-content">
				<div class="conetent-empty" v-show="cartcontent">
					<div v-if='!cartcontent'>
