							<i class="fa fa-shopping-cart"></i>
						</div>
						<span>购物车是空的，赶紧选购吧</span>		
					</div>
					<div class="food-items">
						<div class="food-item" v-for='item in paylist'>
							<span>{{item.name}}</span>
							<span v-if='item.count'>
								<cartbutton :food='item'></cartbutton>
							</span>
							<span>{{item.specfoods[0].price * item.count}}</span>
						</div>
					</div>
				</div>
			</div>
			<div class="cart-bottom">
				<div class="bottom-left" @click='showcartcontent'>
					<span><i class="fa fa-shopping-cart"></i></span>
					<span v-if='totalaccount'><i style="font-size: 12px">￥</i><i  class="totalaccount">{{totalaccount}}</i></span>
					<span>配送费￥4</span>
				</div>
				<div class="bottom-right" :class="carttextcolor" @click='paythebill'>
					<div>
						<span>{{carttext}}</span>
						<span></span>					
					</div>
				</div>
			</div>
