			 			<span>{{data.name}}</span>
			 			<span>{{data.description}}</span>
			 		</a>


			 		<div style="width: 100%;display: flex; flex-wrap:wrap;">
				 		<div class="option-detail" v-for = 'food in data.foods'>
				 			<div class="foodcounter">
				 				<cartbutton :foodid='food.virtual_food_id' :food='food'></cartbutton>
				 			</div>
				 			<div class="option-detail-img">
				 				<img src="../assets/gakki.jpg" @click='showimgdetail(food)'>
				 			</div>

				 			<div class="option-detail-right">
				 				<div>{{food.name}}</div>
				 				<div v-show='food.description'>{{food.description}}</div>
				 				<div>月售{{food.month_sales}}份</div>
				 				<div>￥{{food.specfoods[0].price}}</div>
				 			</div>
				 		</div>			 			
