				            "sold_out": false,
				            "recent_popularity": 14,
				            "is_essential": false,
				            "item_id": "213659672370",
				            "checkout_mode": 1,
				            "specs": [],
				            "partial_reduce_activity_id": null,
				            "stock": 0
				        }],
				        "category_id": 515592199
				    }],
				    "activity": null,
				    "type": 3,
				    "id": -2,
				    "is_activity": null
				}]
			}
		}
	}
</script>

<style type="text/css" lang='less'>
.borderred{
	border: 1px solid red;
	border-radius: 10px;
}
.ellipsis{
	overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
	.detailwrapper{
		background-color: #fff;
		display: flex;
		justify-content: space-between;
		.detail-left{
			position: relative;
			width: 885px;
			border: 1px solid pink;
			.food-option{
				width: 100%;
				display: flex;
				background-color: #eee;
				padding: 10px 0px 5px 0px;
				:hover{
					background-color:pink;
				}
				a{
					display: inline-block;
				    margin: 5px;
				    padding: 3px 10px;
				    width: 18%;
				    border-radius: 2px;
				    color: #666;
				    white-space: nowrap;
				    text-overflow: ellipsis;
				    overflow: hidden;
				    vertical-align: top;
				}
			}
			.food-detail{
				width: 100%;
				.option-container{
					width: 100%;
					.option-header{
						 padding: 20px 0 10px 15px;
   						 text-align: left;
   						 display: block;
   						 span:first-child{
   						 	font-size: 20px;
   						 	font-weight: 700;
   						 	margin-right: 20px;
   						 }
   						 span:last-child{
   						 	font-size: 12px;
   						 	color: #999;
   						 }
					}
					.option-detail{
					   	position: relative;
						.borderred;
						width:44%;
						display: flex;
						text-align: left;
						flex-direction: row;
						margin:0 2% 2% 0 ;
						.foodcounter{
							z-index: 22;
							position: absolute;
							right: 10px;
							bottom: 10px;
						}
						.option-detail-img{
							flex: 0 0 100px;
							width: 100px;
							margin-right: 12px;
							img{
								width: 100px;
								height: 100px;		
								display: block;						
							}
						}
						.option-detail-right{
							font-size: 12px;
							position: relative;
							div:nth-child(1){
								font-size: 14px;
   								font-weight: 700;
   								height: 25%;
   								.ellipsis;
							}
							div:nth-child(2){
								.ellipsis;
   								height: 25%;
								width: 300px;
							}
							div:nth-child(3){
								height: 25%;
							}
							div:nth-child(4){
								height: 25%;
								position: absolute;
								bottom: 0;
								left: 0;
								font-size: 14px;
    							font-weight: 700;
    							color: red;			
							}							
						}
					}
				}
			}
		}
	}
	.shop-cart{
		width: 320px;
		position: fixed;
		right: 0;
		bottom: 0;
		.cart-header{
			display: flex;
			justify-content: space-between;
			height: 45px;
			background-color: #eee;
			line-height: 45px;
			div{
				margin-left: 20px;
				font-size: 14px;
				span:nth-child(2){
					color: #0089dc;
					margin-left:10px;
				}
			}
			.shop-cart-icon{
				height: 20px;
				width: 20px;
				margin-right: 20px;
				color: #0089dc
			}
		}
		.cart-bottom{
			height: 46px;
			display: flex;
			.bottom-left{
				background-color: #2c2c2c;
				flex: 1;
				line-height: 46px;
				text-align: left;
				.totalaccount{
					display: inline-block;
					font-size: 24px;
					color: #fff;
					line-height: 46px;
				}
				span:nth-child(1){
					display: inline-block;
					i{
						font-size: 30px;
						line-height: 46px;
						margin-left: 10px;
						color: white;
					}
				}
				span:nth-child(3){
					font-size: 14px;
					line-height: 46px;
					color: #999;
					margin-left: 5px;
				}
			}
			.bottom-right{
				flex: 0 0 120px;
				width: 120px;
				line-height: 46px;
				color:#e4e4e4;
				color: black;
			}
		}
		.cart-content{
			width: 100%;
			padding: 0 20px;
			box-sizing: border-box;
			.content-cart{
				i{
					font-size: 40px;
					margin:40px 0 20px;
				}

			}
			span{
				font-size: 14px;
				margin-bottom: 40px;
				display: block;
			}
			.food-items{
				width: 100%;
				.food-item{
					display: flex;
					justify-content: space-between;
					position: relative;
					height: 45px;
					font-size:14px;
					line-height: 45px;
					display: flex;
					span:nth-child(2){
						position: absolute;
						top: 0;
						bottom: 0;
						left: 0;
						right: 0;
						margin: auto;
						width: 90px;
						height: 26px;
						display: table-cell;
						line-height: 45px;
					}
					span:nth-child(3){
						width: 40px;
					}
				}
			}
		}
	}
	.graycolor{
		background-color: #e4e4e4;
		color: black;
	}
	.greencolor{
		background-color: #51d862;
		color: white;
	}

</style>


