<style type="text/css" lang='less'>
	.food-card-detail{
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: pink;
		top: 0;
		left: 0;
		margin:auto;
		z-index: 50;
		.card-detail{
			position: absolute;
			margin: auto;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			width: 900px;
			height: 460px;
			background-color: white;
			box-sizing: border-box;
			padding: 30px 40px;
			display: flex;
			.close-icon{
				position: absolute;
				top: 20px;
				right: 20px;
				font-size: 20px;
			}
			.dialog-left{
				flex: 0 0 360px;
				width: 360px;
				margin-right: 30px;
				height: 800px;
				img{
					display: inline-block;
					width: 360px;
					height: 360px;
				}
			}
			.dialog-right{
				width: 430px;
				text-align: left;
				.dialog-name{
					font-size: 22px;
				    color: #333;
				    margin-bottom: .5em;
				}
				.dialog-description{
					border-top: 1px dashed red;
					border-bottom: 1px dashed red;
					font-size: 13px;
					line-height: 1.8em;
					padding: 5px;
				}
				.dialog-buy{
					display:flex;
					justify-content: space-between;
					height: 43px;
					font-size: 28px;
					color: #f17530;
					padding: 20px 0;
				}
				.dialog-rating{
					width: 100%;
					.rating-header{
						font-size: 14px;
					    color: #aaa;
					    border-bottom: 1px solid #ddd;
					    padding-bottom: 0.3em
					}
					.rating-item{
						height: 20px;
					    border-bottom: 1px solid #ddd;
					    padding: 18px 0;
					    color: #999;
					    .ratingstar{
					    	display: inline-block;
					    }
					    >span{
					    	margin-left: 20px;
					    }
					    .ratingtext{
					    	line-height: 26px;
					    }
					}
				}
			}

		}
	}
	.cover{
		height: 200px;
		overflow: hidden;
	}
</style>


