<template>
	<div>
		<div class="rating-items">
			<span class="fa fa-star" v-for='i in redstar' style="color: red"></span><span class="fa fa-star" v-for='i in graystar'></span>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:{
			starnum:{
				type:Number
			}
		},
		computed:{
			redstar(){
				return Math.round(this.starnum)
			},
			graystar(){
				return 5-this.redstar
			}
		}
	}
</script>

<style type="text/css" lang='less'>
	.rating-count{
		/*color: red*/
	}
</style>


